<template>
		<div class="_full_router contain-white">
    <custHeader :title="title"></custHeader>
    			 <ul class="my-hold-container list-auto"><li>
                <div  style="width:36vw;">成交时间</div>
                <div  style="width:19vw;" class="text-right">委托人/金额</div>
                <div  style="width:20vw;" class="text-right">价格/数量</div>
                <div class="text-right">状态</div>
                </li>
                <li v-for="(item,index) in qryList" :class="styleColorByBiz(item.stkBiz)">
                   <div  style="width:36vw;"> <div>{{item.stkName}}</div><div>{{item.matchedTime | timetext}}</div></div>
                   <div style="width:19vw;" class="text-right"><div>{{item.custName}}</div><div >{{item.matchedAmt}}</div></div>
                   <div  style="width:20vw;" class="text-right"><div>{{item.matchedPrice | toMoney}}</div><div>{{item.matchedQty}}</div></div>
                   <div   class="text-right"><div v-remoteoptionsspan="dicts.stkBiz">{{item.stkBiz}}</div><div v-remoteoptionsspan="dicts.orderStatus">{{item.orderStatus}}</div></div>
                </li>

            </ul>
            <div v-show="qryList.length==0" class="jqqd" >
                当前没有成交订单
            </div>
		</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox} from 'mint-ui'
import custMixin from  'util/custMixin'
import custHeader from  'components/custHeader'
export default{
    data(){
       return {
       title:"当日成交",
                 qryList:[
                    ],
                 dicts:{
                      orderStatus:{
                        name:"ORDER_STATUS",
                      },
                      stkBiz:{
                        name:"STK_BIZ"
                      }
                    }
            }
    },
    created:function(){

      this.upCltOrderCancelQry();
    },
     components:{
    custHeader
  },
  mixins:[custMixin],
    methods:{
        upCltOrderCancelQry:function(){
          var date = (new Date()).Format("yyyy-M-d");
            var pars={
                sort:"orderDate",
                order:"desc",
                limit:100,
                offset:0,
                // stkBiz:global.STKBIZ.BUY_IN+","+global.STKBIZ.SELL_OUT,
                stkex:global.scdm,
                // begDate:date.replace(/-/g,"")
            };
            axiosUtil.upCltMatchQry(pars).then(data=>{
                var data=data.data;
                this.qryList=data.rows;
            })
        }
    }
}
</script>
<style type="text/css" scoped>
  .app-content{
    background: #fff;
  }
  .my-hold-container li{
    border-bottom: 1px solid #efefef;
  }
</style>



